code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.black {
    background-color: black;
}

.card {
    
    border: solid 15px white;
    border-radius: 25px;
    margin-top: 1em;
    background-image: url("https://www.transparenttextures.com/patterns/carbon-fibre.png");
    box-shadow: 3px 4px 14px 0px rgba(0,0,0,0.75);

}

.marvel {
    background-color: #e00034;
    color: white;
}

.dc {
    background-color: #0376f2;
    color: white;
}

.company-logo {
    max-height: 200px;
    max-width: 200px;
}

.charImage {
    width: 400px;
    height: 400px;
    margin-top: 1em;
}

.cardTop img {
    /* padding: 1em; */

}

.cardTop h1 {
    text-align: center;
}



.description {
padding: 1em;
}

.indy {
    background-color: grey;
    color:white;
}

.shueisha {
    background-color: #9400D3;
    color: white;
}

.sorry-message {
    color: white;
    background-color: black;
    width: 75%;
    margin: 1em auto;
    
}

@media(max-width: 400px){
    .charImage {
        width: 100%;
        height: 400px;
        margin-top: 1em;
    }
}




.searchbar {
    padding: 0 1em 1em 1em;
    color: white;
    background-color: #757575;
    background-image: url("https://www.transparenttextures.com/patterns/concrete-wall-2.png");
    box-shadow: 2px 6px 14px 1px rgba(0,0,0,0.75);
}

.searchbar h1 {
    margin: 0;
}

.searchbar h2 {
    margin-top: 0;
    margin-bottom: 1em;
}

.searchbar .label {
    margin: 1em;
}

input {
    height: 2.5em;
    border-radius: 4px;
    font-size: 1em;
    background-color:lightgrey;
    border: 3px solid transparent;
    outline: none;
    margin: 1em;
}

textarea {
    margin: 1em;
    border-radius: 4px;
    height: 2.5em;
    font-size: 1em;
    background-color:lightgrey;
    border: 3px solid transparent;
    outline: none;
    resize: none;
}

select {
    padding: 16px 20px;
    height: 3em;
    width: 11em;
    font-size: 1em;
    border: none;
    outline: none;
    border-radius: 4px;
    margin: 1em;
    background-color: lightgrey;
  }

input[type = text]:focus, textarea:focus {
    border: 3px solid #555;
}

input[type = submit] {
    background: red;
    width: 7em;
    color: white;
    cursor: pointer;
}

input[type = submit]:hover {
    border: 3px solid #555;
}

.link-bar {
    background-color: #757575;
    background-image: url("https://www.transparenttextures.com/patterns/concrete-wall-2.png");
}

a {
    text-decoration: none;
    color:white;
}

.fa {
    color: seashell;
}

.fa:hover {
    color: black;
}

.logo {
    margin-top: 1em;
}

/* Modal */

.modal > .close {
    cursor: pointer;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    font-size: 40px;
    font-weight: bold;
    text-align: right;
  }

  .modal {
    color: white;
    background-color: blue;
    background-image: url("https://www.transparenttextures.com/patterns/concrete-wall-2.png");
    flex-direction: column;
  }

  .modal .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: 10px;
    top: 10px;
    font-size: 40px;
    border-radius: 18px;
  
  }

  .modal .close:hover {
    color: black;
  }

  .modal form {
        padding-top: 1.5em;
        font-weight: bold;
        display: flex;
        flex-direction: column;
  }

  .modal input {
      margin: 1em 1em;
  }

  .modal input[type = submit] {
      width: 17em;
    margin: 0 auto 1em auto;  }

  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
.addchar {
    color: white;
    background-color: #757575;
    background-image: url("https://www.transparenttextures.com/patterns/concrete-wall-2.png");
    padding-bottom: 2em;
}

.custom-card .charImage {
    margin-top: 1em;
}

.abilities, .teams {
    text-align:center;
}

.custom-card ul {
    margin-top: 0;
}

.addchar p {
    margin-bottom: 0;
}

.cardTop2 p {
    margin-top: 1em;
    margin-bottom: 1em;
}

.custom-card b {
    margin: 1em auto;
}

.card-container {
    width: 50%;
}

.addchar h1 {
    text-align: center;
}



.green {
    background-color: green;
}

.red {
    background-color: red;
}

.form-container {
    display: flex;
    max-width: 75%;
    flex-wrap: wrap;
    margin: 0 auto 1em auto;
}

.flex {
    display: flex;
}

.cardTop2 {
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.column {
    display: flex;
    flex-direction: column;
}

.row {
    display:flex;
    flex-direction: row;
}

.form-item {
    display: flex;
    flex-direction: column;
    width: 20%;
    align-self: center;
}

.submit-container {
    display: flex;
    height: 114px;
    justify-content: center;
    align-items: center;
    
}

.form-container input[type=submit] {
    width: 175px;
    height: 3em;
    margin-bottom: 0;
}

.addchar select {
    width: 183px;
}


@media(max-width: 1024px) {
    .form-container {
        justify-content: space-around;
    }
    
    .form-item {
        width: 33%
    }

    .submit-container {
        width: 100%;
        height: 5em;
    }

    .form-container input[type=submit] {
        width: 75%;
        height: 90%;
    }
}

@media(max-width: 768px) {
    .form-container {
        justify-content: space-around;
        max-width:95%;
    }
    
    .form-item {
        width: 33%
    }

    .submit-container {
        width: 100%;
        height: 5em;
    }

    .form-container input[type=submit] {
        width: 75%;
        height: 90%;
    }
}

@media(max-width: 400px){
    .charImage {
        width: 100%;
        height: 400px;
        margin-top: 1em;
    }

}

@media(max-width: 1023px) {
    .custom-card {
